import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ColorTypeService } from "app/modules/design/service/colorType.service";
import { ItemMasterService } from "app/modules/item-master/service/itemMaster.service";
import { UserService } from "app/modules/user/service/user.service";
import { ModalAddCustomerComponent } from "app/shared/modal-add-customer/modal-add-customer.component";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { WholeSaleService } from "../../service/wholeSale.service";

@Component({
  selector: "app-whole-sale",
  templateUrl: "./form-whole-sale.component.html",
  styleUrls: ["./form-whole-sale.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class FormWholeSaleComponent implements OnInit {
  contentHeader: object;
  public submitted = false;
  public editable = false;
  public wholeSaleForm: FormGroup;
  public id;
  public customerData = {
    customerName: null,
    customerNumber: null,
  };
  public totalAmount = {
    totalShipmentCost: null,
    totalMiscCost: null,
    totalCost: null,
    totalNetAmount: null,
  };
  itemMaster;
  colorType;
  userDetail;
  items = [];
  public totalQuantity;

  constructor(
    private _itemMaster: ItemMasterService,
    private _colorType: ColorTypeService,
    private _customerService: UserService,
    private fb: FormBuilder,
    private _router: Router,
    private modalService: NgbModal,
    private _sw: SweetalertComponent,
    private _wholeSaleService: WholeSaleService,
    private route: ActivatedRoute
  ) {}

  getItemDetail(value, index) {
        this.items[index].itemUOM = value.UOM;
        this.items[index].itemArticle = value.article;
      
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");
    if (this.id > 0) {
      this.editable = true;
      this.getDetailById();
    }
    this.getItemMaster();
    this.getColorType();
    this.getUserDetails();
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Whole Sale",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Good Receiving Note",
            isLink: true,
            link: "/",
          },
          {
            name: "Form",
            isLink: false,
          },
        ],
      },
    };
  }

  getDetailById() {
    this._wholeSaleService.getDetailById({ id: this.id }).subscribe((res) => {
      this.wholeSaleForm.patchValue({
        transactionDate: res.data.transactionDate,
        receivingDate: res.data.receivingDate,
        supplierName: res.data.supplierName,
        supplierNumber: res.data.supplierNumber,
        totalShipmentCost: res.data.totalShipmentCost,
        totalMiscCost: res.data.totalMiscCost,
        totalCost: res.data.totalCost,
        totalNetAmount: res.data.totalNetAmount,
        note: res.data.note,
      });
      res.data.details.forEach((item) => {
        this.getDetailFormGroup(item);
      });
    });
  }

  getDetailFormGroup(item) {
    const detailFormGroup = this.fb.group({
      id: [item.id],
      itemMaster: [item.itemMaster],
      itemUOM: [item.itemUOM],
      itemArticle: [item.itemArticle],
      itemColor: [item.itemColor],
      itemQuantity: [item.itemQuantity],
      itemRate: [item.itemRate],
      itemAmount: [item.itemAmount],
      itemShipment: [item.itemShipment],
      itemMiscCost: [item.itemMiscCost],
      itemNetRate: [item.itemNetRate],
      itemNetAmount: [item.itemNetAmount],
    });

    (this.wholeSaleForm.get("details") as FormArray).push(detailFormGroup);
    this.items.push(detailFormGroup.value);
  }

  get f() {
    return this.wholeSaleForm.controls;
  }
  makeReactiveForm() {
    this.wholeSaleForm = this.fb.group({
      transactionDate: ["", [Validators.required]],
      receivingDate: ["", [Validators.required]],
      supplierName: [null, [Validators.required]],
      supplierNumber: [null, [Validators.required]],
      totalShipmentCost: ["", [Validators.required]],
      totalMiscCost: ["", [Validators.required]],
      totalCost: ["", [Validators.required]],
      totalNetAmount: ["", [Validators.required]],
      note: [""],
      details: this.fb.array([]),
    });
    if (!this.editable) {
      for (let i = 0; i < 3; i++) {
        this.addItem();
      }
    }
  }

  addItem() {
    const itemsArray = this.wholeSaleForm.get("details") as FormArray;
    this.items.push(this.createItemFormGroup().value);
    itemsArray.push(this.createItemFormGroup());
  }

  // Create a FormGroup for an item
  createItemFormGroup() {
    return this.fb.group({
      itemMaster: [null],
      itemUOM: [""],
      itemArticle: [""],
      itemColor: [null],
      itemQuantity: [""],
      itemRate: [""],
      itemAmount: [""],
      itemShipment: [""],
      itemMiscCost: [""],
      itemNetRate: [""],
      itemNetAmount: [""],
    });
  }

  // Getter for the details FormArray
  get details() {
    return this.wholeSaleForm.get("details") as FormArray;
  }

  /**
   * DeleteItem
   *
   * @param id
   */
  deleteItem(id) {
    for (let i = 0; i < this.details.controls.length; i++) {
      if (this.details.controls.indexOf(this.details.controls[i]) === id) {
        this.details.controls.splice(i, 1);
        this.items.splice(i, 1);
        break;
      }
    }
  }

  onSubmit(formData) {
    this.submitted = true;
    if (this.wholeSaleForm.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._wholeSaleService.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Whole Sale Invoice", `Record Successfully Add..`);
        this.submitted = false;
        this.wholeSaleForm.reset();
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._wholeSaleService.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire("success", "Whole Sale Invoice", `Record Successfully Update..`);
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/whole-sale"]);
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  navigateToBack() {
    this._router.navigate(["/whole-sale"]);
  }

  AddCustomer = () => {
    const modelOpen = this.modalService.open(ModalAddCustomerComponent, {
      centered: true,
      windowClass: "modal modal-primary",
      size: "xl",
    });
    modelOpen.closed.subscribe((close) => this.getUserDetails());
  };

  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data;
      }
    });
  }

  getItemMaster() {
    this._itemMaster.getAll().subscribe((res) => {
      if (res.data) {
        this.itemMaster = res.data;
      }
    });
  }

  getColorType() {
    this._colorType.getAll().subscribe((res) => {
      if (res.data) {
        this.colorType = res.data;
      }
    });
  }

  getTotalQuantity() {
    let count = 0;
    this.items.map((getQty) => (count += getQty.itemQuantity));
    this.totalQuantity = count;
    this.shipmentCalculator();
  }

  getTotalNetAmount() {
    let amountTotal = 0;
    this.items.map((getQty) => (amountTotal += getQty.itemNetAmount));
    this.totalAmount.totalNetAmount = amountTotal;
  }

  selectCustomer(value) {
    this.customerData.customerName = value.full_name;
    this.customerData.customerNumber = value.phone;
  }

  detailCalculator(i) {
    this.items[i].itemAmount =
      this.items[i].itemRate * this.items[i].itemQuantity;
    this.items[i].itemNetRate =
      (this.items[i].itemAmount +
        +this.items[i].itemShipment +
        +this.items[i].itemMiscCost) /
      this.items[i].itemQuantity;
    this.items[i].itemNetAmount =
      this.items[i].itemAmount +
      +this.items[i].itemShipment +
      +this.items[i].itemMiscCost;
    this.getTotalNetAmount();
  }

  shipmentCalculator() {
    this.items.forEach((x, i) => {
      this.items[i].itemShipment = Math.round(
        (this.totalAmount.totalShipmentCost / this.totalQuantity) *
        this.items[i].itemQuantity)
      // ).toFixed(2);
      this.items[i].itemMiscCost = Math.round(
        (this.totalAmount.totalMiscCost / this.totalQuantity) *
        this.items[i].itemQuantity)
      // ).toFixed(2);
    });
  }

  totalCalculator() {
    let inputAmount = this.totalAmount;
    inputAmount.totalCost =
      inputAmount.totalShipmentCost + inputAmount.totalMiscCost;
    this.shipmentCalculator();
  }
}
